<template>
		<div class="aboutUs" id="aboutUs">
			<navWhite></navWhite>
			<div class="binner">
				<img v-bind:src="binner"/>
			</div>
			<div class="introduction">
				<p class="title"></p>
				<p class="subtitle"></p>
				<hr class="hr"/>
				<div class="introduction1">
					<p>
						200068&nbsp;<br>APPAPPbbsAPP&nbsp;<br>&nbsp;<br><br>&nbsp;<br>20151
					</p>
        
				</div>
				<div class="introduction2">
					<div class="introduction_number introduction_left">
						<p><span class="number">200</span>+</p>
						<p><span class="number">80,000</span>+</p>
						<p><span class="number">20,000</span>+</p>
						<p><span class="number"></span></p>
						<p><span class="number">22</span></p>
					</div>
					<div class="introduction_number introduction_right">
            			<p><span class="number">90</span></p>
            			<p><span class="number">120</span></p>
            			<p><span class="number"></span></p>
            			<p><span class="number">22</span></p>
            			<p><span class="number">300</span></p>
        			</div>
				</div>
			</div>
			<div class="connect">
				<p class="connect_title"><a href="mailto:admin@twtstudio.com">admin@twtstudio.com</a></p>
				<p class="connect_address">C301</p>
				<p class="connect_address">118</p>
				<button onclick="window.open('https://support.twtstudio.com')"></button>
			</div> 
			<div class="timeline">
				<p class="title"></p>
				<p class="subtitle"> </p>
				<hr class="hr"/>
				<ul>
					<li v-for="(item, index) in items">
						<span class="year" :class="{hide: index != 0 && item.year == items[index-1].year}">{{item.year}}</span>
						<span class="circle" :class="{hide: index != 0 && item.year == items[index-1].year}"></span>
						<span class="text">{{item.events}}</span>
					</li>
				</ul>
			</div>

			<div class="division">
				<p class="title"></p>
				<p class="subtitle"></p>
				<hr class="hr"/>
				<div  class="division_column">
					<div class="division_card">
						<img src="../assets/division_pe.png"/>
						<p class="division_name"></p>
						<hr/>
						<p class="division_desc">
							<br/><br/><br/>
						</p>
					</div>
					<div class="division_card">
						<img src="../assets/division_android.png"/>
						<p class="division_name">Android</p>
						<hr/>
						<p class="division_desc">
							<br/><br/>Android<br/>
						</p>
					</div>
				</div>
				<div class="division_column division_column_special">
					<div class="division_card division_card_special">
						<img src="../assets/division_ui.png"/>
						<p class="division_name"></p>
						<hr/>
						<p class="division_desc">
							<br/><br/><br/><br/>
						</p>
					</div>
					<div class="division_card">
						<img src="../assets/division_ios.png"/>
						<p class="division_name">iOS</p>
						<hr/>
						<p class="division_desc">
							<br/><br/>iOS<br/>
						</p>
					</div>
				</div>
				<div class="division_column">
					<div class="division_card">
						<img src="../assets/division_frontend.png"/>
						<p class="division_name"></p>
						<hr/>
						<p class="division_desc">
							<br/><br/><br/></p>
					</div>
					<div class="division_card">
						<img src="../assets/division_backend.png"/>
						<p class="division_name"></p>
						<hr/>
						<p class="division_desc">
							<br/><br/><br/>
						</p>
					</div>
				</div>
			</div>
			<footerBlack></footerBlack>
		</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import footerBlack from '@/components/footer'
  export default {
    name: 'aboutus',
    components: { navWhite, footerBlack },
    data () {
      return {
        // introduction: [{}, {}, {}];
        binner: require('../assets/aboutUs_binner.jpg'),
        items: []
      }
    },
    methods: {
      init: function () {
        window.scrollTo(0, 0)
        this.$http.get('milestones')
        .then(function (response) {
          this.items = response.data
        })
        // this.$http.get('introduce')
        // .then(function (response) {
        //   this.introduction = response.data
        // })
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.title {
	font-weight: 500;
	font-size: 23px;
	color: #333333;
	width: 100%;
	text-align: center;
	margin:  1vw auto;
}
.subtitle {
	font-size: 16px;
	color: #333333;
	width: 100%;
	text-align: center;
	color: #999999;
	margin: 1vw auto;
}
.hr {
	width: 130px;
	height: 2px;
	border: 0;
	margin: 0 auto;
	margin-bottom: 30px;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 	
}
@media screen and (max-width: 768px){
	.title {
		font-size: 20px;
	}
	.subtitle {
		font-size: 15px;
	}
	.hr {
		width: 110px;
	}
}
.aboutUs {
	background-color: white;
	min-height: 100vh;
	overflow: hidden;
	width: 100%;
	position: relative;
	padding-bottom: 80px;
	box-sizing: border-box;
}
.binner {
	width: 100%;
	margin: 60px 0 3vw 0;
}
.binner img {
	width: 100%;
}
.introduction {
	width: 80%;
	margin: auto;
	overflow: hidden;
}
.introduction1 {
	width: 100%;
	margin: 2vw auto;
	min-height: 200px;
	/*border: 0.1px solid black;*/
	box-sizing: border-box;
	font-size: 15px;
}
.introduction2 {
	overflow: hidden;
}
.introduction_number {
	width: 45%;
	background-color: #f8f8f8;
	height: 13vw;
	min-height: 250px;
	padding: 0 5.5%;
	box-sizing: border-box;
	font-size: 14px;
}
.number {
	font-size: 20px;
}
.introduction_left {
	float: left;
}
.introduction_right {
	float: right;
}
.connect {
	width: 100%;
	background-color: #f0f2f2;
	margin: 100px 0;
	padding: 1.5vw 10%;
	height: 5vw;
	position: relative;
	min-height: 60px;
}
.connect_title {
	font-size: 1.2vw;
	font-weight: 500;
	margin: 0;
	width: 80%;
}
.connect_address {
	font-size: 1vw;
	margin: 5px 0;
	width: 80%;
}
.connect button {
	-webkit-border-radius: 1.2vw;
	-moz-border-radius: 1.2vw;
	-o-border-radius: 1.2vw;
	border-radius: 1.2vw;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
	color: white;
	font-weight: 300;
	border: 0;
	margin: 23px auto;
	cursor: pointer;
	outline: none;
	height: 2.4vw;
	width: 8vw;
	font-size: 1vw;
	position: absolute;
	top: 1.3vw;
	right:  30vw;
	min-width: 80px;
	min-height: 20px;
	cursor: pointer;
}
.timeline {
	width: 100%;
	max-width: 650px;
	margin: auto;
	margin-bottom: 50px;
}
.timeline ul {
	width: 80%;
	list-style-type:none;
	font-size: 15px;
	padding: 0;
	margin: auto;
	max-width: 650px;
	font-weight: 300;
}
.timeline li {
	position: relative;
}
.year {
	display: inline-block;
	width: 12%;
	text-align: center;
	color: #cccccc;
	letter-spacing: 2px;
}
.text {
	display: inline-block;
	width: 75%;
	border:none;
	border-left: 2px solid #cccccc;
	padding: 5px 5%;
}
.circle {
	display: inline-block;
	width: 10px;
	height: 10px;
	position: absolute;
	top: 11px;
	left: 12.1%;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-o-border-radius: 5px;
	border-radius: 5px;
	background-color: #cccccc;
}
.hide {
	visibility: hidden;
}
@media screen and (max-width: 768px){
	.introduction_number {
		width: 100%;
		margin: 20px auto; 
		max-width: 350px;
		float: none;
	}
	.connect button {
		position: static;
		margin: 7px 0;
		width: 80px;
		height: 20px;
		-webkit-border-radius: 10px;
		-moz-border-radius: 10px;
		-o-border-radius: 10px;
		border-radius: 10px;
	}
	.connect {
		min-height: 130px;
		margin: 50px 0;
	}
	.connect_title {
		font-size: 12px;
	}
	.connect_address {
		font-size: 12px;
	}
	.connect button {
		font-size: 12px;
	}
	.timeline ul {
		font-size: 13px;
		width: 90%;
	}
	.year {
		width: 15%;
		letter-spacing: 0;
	}
	.text {
		width: 70%;
	}
	.circle {
		width: 10px;
		height: 10px;
		top: 9px;
		left: 15%;
		-webkit-border-radius: 5px;
		-moz-border-radius: 5px;
		-o-border-radius: 5px;
		border-radius: 5px;
	}
}
.division {
	width: 100%;
	padding: 5vw 10%;
	background-color: #f8f8f8;
	overflow: hidden;
	box-sizing: border-box;
}
.division_column {
	width: 30%;
	float: left;
}
.division_column_special {
	margin: 0 5%;
}
.division_card {
	width: 100%;
	float: left;
	text-align: center;
	background-color: white;
	margin: 1vw 0;
	height: 27vw;
	padding: 1.5vw 1vw;
	min-height: 300px;
	box-shadow: 0px 3px 5px #dddddd;
	box-sizing: border-box;
}
.division_card_special {
	height: 30vw;
	min-height: 340px;
}
.division_card img {
	width: 50%;
	margin: auto;
}
.division hr {
	width: 50px;
	height: 2px;
	border: 0;
	background-color: #cccccc
}
.division_name {
	color: #333333;
	font-weight: 500;
	font-size: 1.5vw;
}
.division_desc {
	color: #555555;
	font-size: 1vw;
	font-weight: 300;
}
@media screen and (max-width: 768px){
	.division_card{
		height: 50vw;
		max-height: 300px;
		padding: 10px 1vw;
	}
	.division_card_special{
	}
	.division_column{
		width: 100%;
		max-width: 250px;
		float: none;
		margin: auto;
	}
	.year {
		letter-spacing: 0;
	}
	.division_desc {
		font-size: 10px;
	}
	.division_name {
		font-size: 14px;
	}
}
@media screen and (max-width: 768px) {
	.aboutUs {
		padding-bottom: 80px;
	}
}
</style>


